@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  min-width: 320px;
  height: 100%;
  font-family: Arial, sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: #000000;
  background-color: #ffffff;
}
@media (max-width: 768px) {
  .page {
    font-size: 16px;
    line-height: 24px;
  }
}

.page__body {
  max-height: 100%;
  margin: 0;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
}

@media (min-width: 768px) {
  .page-header__nav {
    margin: 0 auto;
    padding: 24px 0 0;
    display: grid;
    grid-template-columns: 174px 1fr;
    gap: 94px;
  }
}
@media (min-width: 1280px) {
  .page-header__nav {
    width: 1220px;
    margin: 0 auto;
    padding: 53px 0 0;
    grid-template-columns: 202px 1fr;
  }
}

.site-menu {
  font-family: "Oswald", Arial, sans-serif;
  font-size: 18px;
  line-height: 1;
  text-align: center;
  text-transform: uppercase;
  list-style: none;
  padding: 0;
}
@media (max-width: 767px) {
  .site-menu {
    position: absolute;
    z-index: 2;
    background-color: #ffffff;
    width: 100%;
    left: 0;
    top: 66px;
    border-top: 1px solid #E6E6E6;
    font-size: 20px;
    font-weight: 500;
    text-align: center;
  }
}
@media (min-width: 768px) {
  .site-menu {
    display: flex;
    gap: 36px;
    justify-content: flex-end;
  }
}

@media (max-width: 767px) {
  .site-menu__item {
    border-bottom: 1px solid #E6E6E6;
  }
}

.site-menu__link {
  display: block;
  padding: 22px;
  color: #000000;
  text-decoration: none;
}
@media (min-width: 768px) {
  .site-menu__link {
    padding: 18px 0 12px;
  }
}
.site-menu__link:hover {
  opacity: 0.6;
}
.site-menu__link:active {
  opacity: 0.3;
}

@media (min-width: 1280px) {
  .site-menu--desktop-main .site-menu__link {
    color: tomato;
  }
}

@media (min-width: 768px) {
  .site-menu__item--current {
    border-bottom: 2px solid #68B738;
  }
}

@media (min-width: 1280px) {
  .site-menu--desktop-main .site-menu__item--current {
    border-bottom: 2px solid #ffffff;
  }
}

@media (max-width: 767px) {
  .main-nav__bar {
    position: relative;
    margin: 0 auto;
    padding: 12px 0 17px;
    display: grid;
    grid-template-columns: 34px 1fr 34px;
    gap: 16px;
  }
  .main-nav__bar::before {
    position: absolute;
    content: "";
    width: 100vw;
    height: 1px;
    background-color: #D9D9D9;
    left: calc(50% - 50vw);
    bottom: 0;
  }
}

.main-nav__logo {
  grid-column: 1/3;
}

.main-nav__menu {
  margin: 0;
}

.main-nav__toggle {
  position: relative;
  cursor: pointer;
  border: none;
  background-color: transparent;
  padding: 0;
  margin-top: 10px;
  width: 24px;
  height: 18px;
}
.main-nav__toggle:hover {
  opacity: 0.8;
}
.main-nav__toggle:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  width: 24px;
  height: 2px;
  background-color: #000000;
  left: 0;
  top: 0;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}

.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  width: 24px;
  height: 2px;
  background-color: #000000;
  left: 0;
  top: 8px;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}

@media (max-width: 767px) {
  .main-nav--closed .site-menu {
    display: none;
  }
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

@media (max-width: 767px) {
  .main-nav--nojs .site-menu {
    position: static;
    display: block;
  }
}

.page-main__header {
  padding-top: 28px;
  margin-bottom: 40px;
}
@media (min-width: 768px) {
  .page-main__header {
    padding-top: 65px;
    margin-bottom: 66px;
  }
}
@media (min-width: 768px) {
  .page-main__header {
    padding-top: 74px;
  }
}

.page-main__heading {
  font-family: "Oswald", Arial, sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 40px;
  margin: 0 0 40px;
}
@media (min-width: 768px) {
  .page-main__heading {
    font-size: 60px;
    line-height: 60px;
    margin-bottom: 32px;
  }
}

.page-main__subheading {
  font-family: "Oswald", Arial, sans-serif;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  margin: 0;
}
@media (min-width: 768px) {
  .page-main__subheading {
    font-size: 20px;
    line-height: 30px;
    margin-top: 54px;
  }
}

.container {
  width: 87.5%;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 768px) {
  .container {
    width: 92.2%;
  }
}
@media (min-width: 1280px) {
  .container {
    width: 1220px;
  }
}

@media (min-width: 768px) {
  .container--form {
    width: 82%;
  }
}
@media (min-width: 1280px) {
  .container--form {
    width: 1220px;
  }
}

.logo:not(.logo--footer) {
  display: grid;
  grid-template-columns: 34px 1fr;
  gap: 16px;
  align-items: center;
}
@media (min-width: 768px) {
  .logo:not(.logo--footer) {
    grid-template-columns: 61px 1fr;
  }
}
@media (min-width: 1280px) {
  .logo:not(.logo--footer) {
    grid-template-columns: 71px 1fr;
  }
}
.logo:hover {
  opacity: 0.8;
}
.logo:active {
  opacity: 0.6;
}
.logo img {
  display: block;
}

.logo__image {
  width: 34px;
  height: auto;
}
@media (min-width: 768px) {
  .logo__image {
    width: 61px;
  }
}
@media (min-width: 1280px) {
  .logo__image {
    width: 71px;
  }
}

.logo__sign {
  justify-self: center;
}
@media (min-width: 1280px) {
  .logo__sign {
    justify-self: left;
  }
}

.logo--footer {
  display: flex;
  align-items: center;
  cursor: pointer;
}
@media (max-width: 767px) {
  .logo--footer {
    min-height: 95px;
    justify-content: center;
  }
}